import React, { Component } from "react";
import CodeSection from "../../CodeSection";
import styled from "styled-components";

const Favicon = styled.div`
  width: 350px;
  height: 350px;
  display: flex;
  justify-content:center;
  alig
`;

class YoutubeVideoApi extends Component {
  render() {
    return (
      <div>
        <CodeSection>{`import React, { Component } from "react";
    import { Dialog, Button } from "dawn-react-ui-lib";
    
    class Example extends Component{
        state={
          open: false
        }
        // 使用者自行定義的Button的點擊事件
        openDialog = () => {
          this.setState({
            open:true
          })
        }
        // 傳下去給Dialog關閉畫面的事件
        closeDialog = () => {
          this.setState({
            open:false
          })
        }
        render(){
            return(
              <div>
                <Button
                  text="用戶確認對話框"
                  onClick={this.openDialog}
                  width="250px"
                  padding="16px"
                />
                <Dialog
                  open={this.state.open}
                  handleClose={this.closeDialog}
                  contentHeader="標題文字"
                  contentBody={<div>內容</div>}
                  userAgree
                  userAgreeFunc={() => {
                    alert("自定義確認事件");
                  }}
                />
              </div>
            )
        }
    }
    
    export default Example;`}</CodeSection>
      </div>
    );
  }
}

export default YoutubeVideoApi;
